nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #0f172a;
    color: white;
    position: fixed; 
    top: 0; 
    width: 100%; 
    padding: 0px 50px;
    z-index: 1000; 
}

nav .title {
    font-size: 1.5rem;
    margin: 1rem;
    font-weight: bold;
    text-decoration: none;
    color: white;
    font-style: italic;
}

nav ul {
    display: flex;
    @media (max-width: 722px) {
        display: none;
    }
}

nav ul.open2 {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 0.25rem;
}

nav ul li {
   list-style: none; 
   position: relative;
}

nav ul li a {
    display: block;
    text-decoration: none;
    color: white;
    padding: 0.5rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
    min-width: 150px;
    text-align: center;
    transition: all 0.3s ease-in-out;
}
.dropDownNavbarOptions{
    width: 150px;
}
.dropDownNavbarOptions::after {
    display: inline-block;
    position: relative;
    left: 10px;
    content: '>';
    font-weight: 700;
    transition: all 0.15s ease-in-out;
}
.dropDownNavbarOptions ul{
    width: 50%;
}

.open::after {
    display: inline-block;
    position: relative;
    left: 10px;
    content: '>';
    font-weight: 700;
    transition: all 0.15s ease-in-out;
    transform: rotate(90deg);
}

nav ul li a:hover {
    background-color:#000000;
}

nav .menu {
    display: none;
    position: absolute;
    top: 0.75rem;
    right: 0.5rem;
    flex-direction: column;
    justify-content: space-between;
    width: 2.25rem;
    height: 2rem;
}

nav .menu span {
    height: 0.4rem;
    width: 100%;
    background-color: #fff;
    border-radius: 0.2rem;
}

.submenu {
    display: none;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    background-color: rgb(0, 0, 0);
    position: absolute;
    color: rgb(255, 255, 255);
    border-radius: 0.5rem;
    left: 8px;
    border-top-right-radius: 0px;
    border-top-left-radius: 0px;
    transition: all 0.3s ease-in-out;
    
  
    /* Remove fixed width */
    /* width: 140px; */
}
.submenu li{
    margin-top: 10px;
    margin-bottom: 10px;
}

.submenu.open2 {
    /* Set width to match the parent menu container */
    width: 150px;
    
}
.submenu li{
    width: 100%;
}

.submenu li:hover{
    background-color: red;
}

nav ul li div {
    display: block;
    text-decoration: none;
    color: white;
    padding: 0.5rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
    width: 150px;
    text-align: center;
    transition: all 0.3s ease-in-out;
}



nav ul li div:hover {
    background-color:#000000;
    cursor: pointer;
}

@media (max-width: 722px) {
    nav .menu {
        display: flex;
        
    }

    nav {
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
    }

    nav ul {
        display: none;
        flex-direction: column;
        width: 100%;
        margin-bottom: 0.25rem;
        
    }

    nav ul.open {
        display: flex;
        
    }

    nav ul li {
        width: 100%;
        text-align: center;
        justify-content: center;
    }

    nav ul li a {
        margin: 0.2rem 0.5rem;
    }
}
